import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FFileDropDirective, FFileSelectDirective, FFilePreviewListTypeTemplateDirective, FFileUploadDirective, FFilePreviewToolbarTemplateDirective } from './directive/index';
import { FFileUploadComponent, FFileViewImageComponent, FFilePreviewComponent, PreviewCheckboxComponent } from './components/index';
import { UploadServerService } from './model/index';
import { FFileSizePipe, FFileDatePipe } from './pipe/index';
import { FFileUploadServerConfig, FFileUploadAPIServiceToken, FFileUploadDefaultService, FFileUploaderService } from './service/index';
import { NotifyModule } from '@farris/ui-notify';
import { ModalModule } from '@farris/ui-modal';
import { DateHelperModule } from '@farris/ui-common/date';
import { MessagerModule } from '@farris/ui-messager';
import { FAreaResponseModule } from '@farris/ui-area-response';
import { UploadAndPreviewComponent } from './components/upload-and-preview/upload-and-preview.component';
import { UploadProgressComponent } from './components/upload-progress/upload-progress.component';
import { MultiDownloadInfoComponent } from './components/multi-download-info/multi-download-info.component';


@NgModule({
  declarations: [FFileDropDirective, FFileSelectDirective, FFileUploadComponent, FFileViewImageComponent, FFilePreviewComponent, FFilePreviewListTypeTemplateDirective, FFileUploadDirective, PreviewCheckboxComponent, FFilePreviewToolbarTemplateDirective, FFileSizePipe, UploadAndPreviewComponent, UploadProgressComponent, FFileDatePipe,MultiDownloadInfoComponent],
  exports: [FFileDropDirective, FFileSelectDirective, FFileUploadComponent, FFileViewImageComponent, FFilePreviewComponent, FFilePreviewListTypeTemplateDirective, FFileUploadDirective, FFilePreviewToolbarTemplateDirective, FFileSizePipe, UploadAndPreviewComponent, UploadProgressComponent, FFileDatePipe],
  imports: [
    CommonModule, NotifyModule, MessagerModule, FAreaResponseModule, DateHelperModule.forRoot(), ModalModule.forRoot()
  ],
  providers: [
    FFileUploaderService,
    {
      provide: UploadServerService,
      useClass: FFileUploadDefaultService
    }
  ],
  entryComponents:[
    MultiDownloadInfoComponent
  ]
})
export class FFileUploadModule {
  static forRoot(config?: FFileUploadServerConfig, uploadSer?: any): ModuleWithProviders {
    return {
      ngModule: FFileUploadModule,
      providers: [
        { provide: FFileUploadAPIServiceToken, useValue: config },
        FFileUploaderService,
        {
          provide: UploadServerService,
          useClass: uploadSer || FFileUploadDefaultService
        }
      ]
    };
  }
}
